@import url('https://fonts.googleapis.com/css?family=Inconsolata:400,700');

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  font-family: 'Inconsolata', monospace;
  font-size: 16px;
}

a, a:visited {
  color: #999;
}

a:active, a:hover {
  color: #11A0C4;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-bounce infinite 1s linear;
          animation: App-logo-bounce infinite 1s linear;
  height: 80px;
  margin-bottom: 10px
}

.App-header {
  background-color: #FFDB3D;
  padding: 20px;
  color: #222;
}

.App-title {
  font-size: 28px;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-bounce {
  0% { -webkit-transform: translateY(0px); transform: translateY(0px); }
  50% { -webkit-transform: translateY(25px) scaleY(0.9); transform: translateY(25px) scaleY(0.9); }
  100% { -webkit-transform: translateY(0); transform: translateY(0); }
}

@keyframes App-logo-bounce {
  0% { -webkit-transform: translateY(0px); transform: translateY(0px); }
  50% { -webkit-transform: translateY(25px) scaleY(0.9); transform: translateY(25px) scaleY(0.9); }
  100% { -webkit-transform: translateY(0); transform: translateY(0); }
}

.App-recipe {
  padding-bottom: 100px;
}

.btn {
  font-family: inherit;
  padding: 10px 20px;
  font-size: 20px;
  border-radius: 5px;
  cursor: pointer;
  outline: none;
  border: none;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.btn:hover {
  -webkit-box-shadow: 0px 1px 2px 1px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 1px 2px 1px rgba(0, 0, 0, 0.2);
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

.btn-primary {
  background-color: #11A0C4;
  color: white;
}

.btn-primary:hover {
  background-color: #357AA5;
}

.seed {
    -ms-flex-align: center;
        align-items: center;
    text-align: center;
}

input[type=number] {
  font-family: 'Inconsolata', monospace;
  font-size: 14px;
  text-align: center;
  font-style: italic;
  padding: 0px 0px 0px 0px;
  background: transparent;
  outline: none;
  border: none;
  border-bottom: 1px dashed #83A4C5;
  width: 50px;
  overflow: hidden;
  resize:none;
} 

label {
  font-family: 'Inconsolata', monospace;
  font-size: 14px;
  font-style: italic;
}

.card {
  margin-top: 10px;
}

.card-block {
  text-align: left;
  margin: auto;
  width: 50%;
  -webkit-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}

ol li {
  line-height: 1.4;
}

.App-footer {
  position: fixed;
  background-color: #222;
  color: white;
  width: 100%;
  bottom: 0;
  text-align: right;
  font-size: 12px;
  padding: 5px 20px;

}

.example-enter {
  opacity: 0.01;
}

.example-enter.example-enter-active {
  opacity: 1;
  -webkit-transition: opacity 0.3s ease-in-out;
  -o-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;
}

.example-leave {
  opacity: 1;
}

.example-leave.example-leave-active {
  opacity: 0.01;
  -webkit-transition: opacity 0.3s ease-in-out;
  -o-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;
}



